
.switch[data-v-7ccd75e3]{    position: relative;
    width: 70px;
    height: 30px;
    border: 1px solid #e1e1e1;
    background: #fff;
    border-radius: 20px;    float: right;
    margin: 10px 0;
    outlinenone
    transition-duration: 0.2s;
}
.switch.active[data-v-7ccd75e3]{
    	background: #ff8500;
    	transition-duration: 0.2s;
}
.switch.active[data-v-7ccd75e3]:before{ content:'on';right: auto;
    position: absolute;
    box-sizing: border-box;
        width: 90px;
    height: 30px;
    line-height: 30px;
    left: 15px; color: #fff;font-weight: 400;
}
.switch[data-v-7ccd75e3]:after{ content:'';
}
.switch .btn[data-v-7ccd75e3]{ position: absolute;
